.container--contact {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;

  @include for-tablet {
    flex-direction: row;
  }
}

.button--contact {
  margin-top: 55px;

  &:focus {
    background-color: #3c8ea9;
  }
}

.contact {
  &__left {
    width: 100%;

    @include for-tablet {
      width: 51%;
    }
  }

  &__right {
    width: 100%;

    @include for-tablet {
      width: 35%;
    }
  }

  &--subtitle {
    margin: 0 !important;
  }

  &__title {
    font-weight: 800;
    font-size: 12.6px;
    line-height: 150%;
    color: #334563;
    text-transform: uppercase;
    margin-top: 43px;
  }

  &__text {
    font-weight: 900;
    font-size: 24px;
    line-height: 150%;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $color-main-blue;
  }

  &__link {
    position: relative;
    padding-bottom: 8px;

    @include for-tablet {
      &::after {
        position: absolute;
        bottom: 0;
        left: 0;
        content: "";
        width: 100%;
        height: 4px;
        background-color: #fcc304;
        border-radius: 32px;
        animation: zoom-out;
        animation-duration: $hover-duration;
        animation-fill-mode: both;
      }

      &:hover {
        &::after {
          animation-name: zoom-in;
        }
      }
    }
  }
}
