.header {
  margin-bottom: 85px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  @include for-tablet {
    flex-direction: row;
    justify-content: space-between;
  }
  @include for-desktop {
    justify-content: flex-start;
  }

  &__logo {
    font-weight: bold;
    font-size: 32px;
    line-height: 100%;
    color: #fff;

    @include for-desktop {
      margin-right: 150px;
    }
  }
}
